:root{
  --color-black-foreground:#1A1A1A;
  --color-charcol:#282828;
  --color-white: #FFFFFF;
  --color-transparent:transparent;
  --color-yellow-pineapple:#FEE12B;
  --color-blue-visited-1:#092ad0;

}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-black-foreground);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Spacing shortcuts */

.udc{
  display: flex;
  justify-content: center;
  align-items: center;
}

.udc-left{
  display: flex;
  justify-content: start;
  align-items: center;
}

.sb{
  display:flex;
  justify-content: space-between;
}

.fdc{
  display: flex;
  flex-direction: column;
}

.fdr{
  display: flex;
  flex-direction: row;
}

.fw{
  display: flex;
  flex-wrap: wrap;
}


/* general design styles */

#banner{
  min-height: 60px;
  display: flex;
  justify-content: space-evenly;
}



#banner > div>  *{
  margin-left:.5rem;
  margin-right:.5rem;
}



/* designs for each of the pannels on main page */




.font-color{
  color:var(--color-white);
}

.page-body{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: space-between;

}

#page-left{
  display:flex;
  justify-content: center;
  width:39%;
  height:100%;
  background-color: var(--color-charcol);

}

#page-right{
  width:60%;
  height:100%;
  background-color: var(--color-charcol);

}

.adjbar{
  height:100%;
  width:8px;
}
.sq-buttons{
  background-color: var(--color-charcol);
  color: var(--color-white);
  border-radius: .5rem;
  border: 0;
}

.banner-button{
  height:2rem;
  width:8rem;
}



/* general modal styles */

.active-modal {
  overflow:hidden;
  overflow-y:auto;
}

.modal-overlay{
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  background-color: var(--color-transparent);
  transition: opacity 0.4s ease-in-out 0s;
}

.modal-content{

  position: absolute;
  background-color: var(--color-white);
  left:600px;
  width:180px;
  height:auto;
  margin-top: 4%;
  border-radius: 1rem;
}

.toolbar{ 
  margin-left:2rem;
  margin-right:2rem;   
}